import Authorization from "./user/auth.service";

const HomeService = {
    async getSearchInfo() {
        try {
            const hotels = await this.getHotels(),
                destinations = await this.getDestinations()
                return {
                    hotels: (hotels == undefined) ? hotels : [],
                    destinations: (destinations == undefined) ? destinations : [],
                }
        } catch (e) {
            console.log(e)
        }
    }, 
    async getHotels(pole_id=undefined) {
        try {
            const url = (pole_id == undefined) ? `/hotels` : `/hotels/by-pole/${pole_id}`
            const hotels = [],
                res = await Authorization.checkLogin(url)
            if(res){
                if(res.status == 200) {                    
                    for(let h of res.data) {
                        hotels.push({
                            text: h.alias,
                            value: h.id,
                        })
                    }                    
                }
            }
            return hotels
        } catch (e) {
            console.log(e)
        }
    },
    async getDestinations() {
        try {
            const {status, data} = await Authorization.checkLogin(`/poles`)
            
            if(status == 200) {
                const destinations = []
                for(let d of data) {
                    destinations.push({
                        text: d.alias,
                        value: d.id,
                    })
                }
                return destinations
            }
        } catch (e) {
            console.log(e)
        }
    },   
    async getOffers() {
        try {
            const res = await Authorization.checkLogin(`/offers`)
            const list = []
            if(res) {
                if (res.status == 200) {
                    for (let item of res.data) {
                        if(item.active) {
                            const offer = {
                                id: item.id,
                                name: item.title,
                                keyword: item.keyword,
                                description: item.description,
                                info: item.info,
                                price: item.price_from,
                                country: "Cuba",
                                btn: 'contactar'
                            }
                            if(item.hasOwnProperty('image')) {
                                for(let i of item.image[0].resources) {
                                    if(i.image == 'original') {
                                        offer.img = i.url  
                                    }
                                }
                            }
                            
                            list.push(offer)
                        }
                    }
                } else //! Fail check login
                {
                    return {
                        success: false,
                        errors: 'Fail check login'
                    }
                }
            }
            return list
        } catch (e) {
            console.log(e)
        }
    },
}

export default HomeService