<template>
    <div>
        <div class="grid gap-2 text-left text-base" v-if="(star_style == 'block')">
            <span class="font-semibold">Categoria de Hotel</span>
            <div class="stars flex justify-between gap-x-2 cursor-pointer">
                <span :class="{'yellow-star': (active_star >= 1)}" @click="toggleStar(1)" class="bg-star w-8 h-8"></span>
                <span :class="{'yellow-star': (active_star >= 2)}" @click="toggleStar(2)" class="bg-star w-8 h-8"></span>
                <span :class="{'yellow-star': (active_star >= 3)}" @click="toggleStar(3)" class="bg-star w-8 h-8"></span>
                <span :class="{'yellow-star': (active_star >= 4)}" @click="toggleStar(4)" class="bg-star w-8 h-8"></span>
                <span :class="{'yellow-star': (active_star == 5)}" @click="toggleStar(5)" class="bg-star w-8 h-8"></span>            
            </div>        
        </div>
        <div class="flex" v-if="(star_style != 'block')">
            <div class="relative text-3xl font-thin">
                <i class="far fa-star"></i>
                <span class="absolute text-orange text-base top-12px left-8px" v-show="(active_star >= 1)"><i class="fas fa-star"></i></span>
            </div>
            <div class="relative text-3xl font-thin">
                <i class="far fa-star"></i>
                <span class="absolute text-orange text-base top-12px left-8px" v-show="(active_star >= 2)"><i class="fas fa-star"></i></span>
            </div>
            <div class="relative text-3xl font-thin">
                <i class="far fa-star"></i>
                <span class="absolute text-orange text-base top-12px left-8px" v-show="(active_star >= 3)"><i class="fas fa-star"></i></span>
            </div>
            <div class="relative text-3xl font-thin">
                <i class="far fa-star"></i>
                <span class="absolute text-orange text-base top-12px left-8px" v-show="(active_star >= 4)"><i class="fas fa-star"></i></span>
            </div>
            <div class="relative text-3xl font-thin">
                <i class="far fa-star"></i>
                <span class="absolute text-orange text-base top-12px left-8px" v-show="(active_star >= 5)"><i class="fas fa-star"></i></span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Stars',
    props: {
        star_style: {
            require: false,
            default: 'blocks'
        },
        star_init: {
            require: false,
            default: 3
        }
    },
    data() {
        return {
            active_star: this.star_init
        }
    },
    methods: {
        toggleStar(number) {    
            this.active_star = number
            // console.log(this.active_star)  
            this.$emit('getStar', number)
        }
    }
}
</script>
<style scoped>
    .bg-star {
        background-image: url('../../assets/img/gray-star.png');
        background-size: contain;
        background-repeat: no-repeat;
    }
    .yellow-star {        
        background-image: url('../../assets/img/yellow-star.png') !important;        
    }
    
</style>